body {
	color: #091e42;
}
.main-header {
	background: #0052cc;
	height: 70px;
	display: flex;
	justify-content: space-between;
	padding: 0 20px;
	.logo {
		max-width: 100%;
		max-height: 100%;
		svg {
			display: inline-block;
			height: 70px;
			fill: #fff;
		}
	}

	.login {
		display: flex;
		align-items: center;
		font-size: 14px;
	}
	.help {
		display: inline-block;
		color: #fff;
		padding: 14px 10px 8px 30px;
		cursor: pointer;
		margin-right: 5px;
		font-size: 14px;
		position: relative;
		.fa-question-circle {
			position: absolute;
			top: 13px;
			left: 6px;
			width: 20px;
			height: 20px;
		}
		&:hover {
			background-color: #0065ff;
		}
		&:active {
			background-color: #4c9aff;
		}
	}
}

.login-link {
	color: #fff;
	padding: 14px 10px 8px;
	outline: none;
	text-decoration: none;
	&:hover {
		background-color: #0065ff;
	}
	&:active {
		background-color: #4c9aff;
	}
}

.top-header {
	height: 370px;
	background-color: #0052cc;
	margin-bottom: 20px;
	.main-title {
		font-size: 48px;
		color: #fff;
		font-weight: 500;
		line-height: 1.14285714;
		margin-top: 52px;
	}

	.search-text {
		position: relative;
	}
	.search-box {
		font-size: 18px;
		height: 2.64285714em;
		padding-left: 55px;
		width: 500px;
		background: transparent;
		border: none;
		border-bottom: 2px solid #fff;
		box-sizing: border-box;
		color: #fff;
		outline: none;
		text-overflow: ellipsis;
		transition: width .2s ease;
		&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
			color: #fff;
			font-weight: 300;
		}
		&::-moz-placeholder { /* Firefox 19+ */
			color: #fff;
			font-weight: 300;
		}
		&:-ms-input-placeholder { /* IE 10+ */
			color: #fff;
			font-weight: 300;
		}
		&:-moz-placeholder { /* Firefox 18- */
			color: #fff;
			font-weight: 300;
		}
	}

	.search-glass {
		color: #fff;
		position: absolute;
		pointer-events: none;
		top: 5px;
		left: 7px;
		fill: #fff;
		svg {
			height: 32px;
			width: 32px;
			max-height: 100%;
			max-width: 100%;
		}
	}

	.top-header-img {
		position: relative;
		height: 100%;
		img {
			position: absolute;
			bottom: -20px;
			right: 0;
			display: block;
			width: 550px;
		}
	}

	.grid-container {
		height: 100%;
	}
	.grid-x {
		height: 100%;
	}
}
.main-header {
	.search-text {
		position: relative;
	}
	.search-box {
		font-size: 14px;
		height: 2.5em;
		padding-left: 40px;
		margin-right: 15px;
		width: 300px;
		background: transparent;
		border: none;
		border-bottom: 2px solid #fff;
		box-sizing: border-box;
		color: #fff;
		outline: none;
		text-overflow: ellipsis;
		transition: width .2s ease;
		&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
			color: #fff;
			font-weight: 300;
		}
		&::-moz-placeholder { /* Firefox 19+ */
			color: #fff;
			font-weight: 300;
		}
		&:-ms-input-placeholder { /* IE 10+ */
			color: #fff;
			font-weight: 300;
		}
		&:-moz-placeholder { /* Firefox 18- */
			color: #fff;
			font-weight: 300;
		}
		&:focus {
			width: 500px;
		}
	}

	.search-glass {
		color: #fff;
		position: absolute;
		pointer-events: none;
		top: 5px;
		left: 4px;
		fill: #fff;
		svg {
			height: 24px;
			width: 24px;
			max-height: 100%;
			max-width: 100%;
		}
	}
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.main-content {
	margin-top: 50px;
}

.main-aside {
	.nav-title {
		padding: 0 0 4px 0;
		font-weight: 600;
		line-height: 1.14285714;
		letter-spacing: -.003em;
		margin-top: 16px;
		color: #505f79;
		margin-bottom: 0;
		font-size: 14px;
		text-transform: uppercase;
	}
	.nav-links {
		li {
			.nav-link {
				display: inline-block;
				margin-left: 3px;
				padding: 7px 26px 7px 5px;
				line-height: 20px;
				font-size: 14px;
				text-decoration: none;
				color: #0052cc;
				&:hover {
					color: #0065ff;
					outline: 0;
					text-decoration: underline;
				}
			}
		}
	}
}

.info-wrapper {
	padding-top: 10px;
	h2 {
		font-size: 24px;
		font-weight: 500;
		line-height: 1.17;
		margin: 0;
		color: #253858;
	}
	.categories-list {
		display: flex;
		justify-content: space-between;
		margin: 25px 0 45px;
		.item {
			text-decoration: none;
			width: 31%;
			border-radius: 3px;
			border: 1px solid #a5adba;
			.cover {
				height: 176px;
				border-radius: 3px;
			}
			.cover-integration {
				background: url("/images/img-1.jpg") 50% 50% no-repeat;
				background-size: cover;
			}
			.cover-design {
				background: url("/images/img-2.jpg") 50% 50% no-repeat;
				background-size: cover;
			}
			.cover-service {
				background: url("/images/img-3.jpg") 50% 50% no-repeat;
				background-size: cover;
			}
			.item-title {
				display: flex;
				align-items: center;
				justify-content: center;
				height: 96px;
				margin: 0;
				font-size: 24px;
				font-weight: 500;
				line-height: 1.17;
				color: #253858;
			}
		}
	}
	.apps-list {
		display: flex;
		justify-content: space-between;
		margin: 25px 0 25px;
		flex-wrap: wrap;
		.item {
			text-decoration: none;
			width: 32%;
			border-radius: 3px;
			padding: 10px 15px;
			box-shadow: 0 4px 8px -2px rgba(9,30,66,.28), 0 0 1px rgba(9,30,66,.3);
			color: #42526e;
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: 20px;
			&:hover {
				background: #f4f5f7;
			}
			.logo-wrap {
				width: 72px;
				height: 72px;
				padding: 8px;
				border: 1px solid #ccc;
				border-radius: 3px;
				margin-top: 10px;
				box-sizing: content-box;
				position: relative;
				.logo {
					width: 100%;
					height: 100%;
					background: url("https://marketplace-cdn.atlassian.com/files/images/68a717f9-3f46-4bbf-bdbf-03df43e5d7ad.png") 50% 50% no-repeat;
				}
				.company-logo {
					position: absolute;
					background: url("/images/fb.png") no-repeat 50% 50%;
					background-size: contain;
					width: 24px;
					height: 24px;
					right: -12px;
					top: -12px;
				}
			}
			.title {
				color: #172b4d;
				font-size: 16px;
				font-weight: 700;
				overflow: hidden;
				text-overflow: ellipsis;
				margin: 10px 0 3px;
				line-height: 1.42857143;
				max-height: 42px;
				width: 100%;
			}
			.desc {
				color: #42526e;
				font-size: 14px;
				word-wrap: break-word;
				overflow: hidden;
				min-height: 54px;
				line-height: 1.26;
				margin: 0;
			}
			.tags {
				width: 100%;
				.tag {
					display: inline-block;
					margin-right: 5px;
					color: #7a869a;
					font-size: 12px;
				}
			}
			.item-footer {
				display: flex;
				width: 100%;
				justify-content: space-between;
				align-items: end;
				font-size: 12px;
				color: #7a869a;
				margin-top: 3px;
			}
			.review-stars {
				display: inline-block;
				width: 69px;
				height: 17px;
				background: url("/images/stars.png") no-repeat;
				background-size: contain;
				margin-right: 10px;
				vertical-align: bottom;
			}
			.text {
				vertical-align: bottom;
			}
		}
		.subscribe {
			background-color: #0052cc;
			padding: 32px 26px;
			&:hover {
				background: #0052cc;
			}
			.sub-title {
				font-size: 28px;
				color: #fafbfc;
			}
			.subscribe-button {
				width: 156px;
				background-color: #ffc400;
				color: #505F79;
				font-weight: 600;
				height: 40px;
				margin: auto;
				border-radius: 3px;
				text-decoration: none;
				font-size: 14px;
				text-align: center;
				line-height: 39px;
				&:hover {
					background: #ffab00;
				}
			}
		}
	}
	.apps-list-short {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin: 25px 0 25px;
		.item {
			font-size: 12px;
			text-decoration: none;
			color: #7a869a;
			width: 24%;
			margin-bottom: 20px;
			border-radius: 3px;
			&:hover {
				background: #f4f5f7;
			}
			.logo-wrap {
				display: inline-block;
				vertical-align: top;
				width: 46px;
				height: 46px;
				margin-right: 12px;
				.logo {
					width: 100%;
					height: 100%;
					background: url("https://marketplace-cdn.atlassian.com/files/com.gliffy.integration.confluence/icons/default/02be7497-e41f-404c-a5bd-97bfa24f942e.png") 50% 50% no-repeat;
					background-size: contain;
				}
			}
			.info-wrap {
				display: inline-block;
				width: calc(100% - 58px);
				margin-left: -4px;
				vertical-align: top;
				.title {
					color: #091e42;
					max-height: 34px;
					overflow: hidden;
					font-weight: 500;
					font-size: 14px;
				}
				.desc {
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
				}
				.review-stars {
					display: inline-block;
					width: 69px;
					height: 17px;
					background: url("/images/stars.png") no-repeat;
					background-size: contain;
					margin-right: 10px;
					vertical-align: bottom;
					margin-top: 5px;
				}
			}
		}
		.more-apps {
			display: inline-block;
			margin: 0 auto;
			color: #0052cc;
			&:hover {
				text-decoration: none;
				color: #0065ff;
				outline: 0;
			}
		}
	}
}

.footer-nav {
	background: #f4f5f7;
	padding: 40px 0 10px;
	border-bottom: 2px solid #c1c7d0;
	.nav-title {
		color: #172b4d;
		font-size: 16px;
		font-weight: 700;
		text-transform: uppercase;
		&.mail-title {
			text-transform: none;
		}
	}
	.nav-links {
		margin: 1em 0;
		.nav-link {
			color: #6b778c;
			font-size: 16px;
			font-weight: 400;
			line-height: 2.1;
			text-decoration: none;
			outline: 0;
			&:hover {
				text-decoration: underline;
			}
		}
	}
	.subscribe-button {
		display: block;
		width: 156px;
		background-color: #0052CC;
		color: #fff;
		font-weight: 400;
		height: 40px;
		border-radius: 3px;
		text-decoration: none;
		font-size: 14px;
		text-align: center;
		line-height: 39px;
		&:hover {
			background: #0065ff;
		}
	}
}

.page-detail {
	.detail-header-wrap {
		background: #f4f5f7;
	}
	.detail-header {
		display: flex;
		padding: 10px 0;
		.logo-wrapper {
			.logo-wrap {
				width: 72px;
				height: 72px;
				padding: 15px;
				border: 1px solid #ccc;
				border-radius: 3px;
				box-sizing: content-box;
				position: relative;
				.logo {
					width: 100%;
					height: 100%;
					background: url("https://marketplace-cdn.atlassian.com/files/images/68a717f9-3f46-4bbf-bdbf-03df43e5d7ad.png") 50% 50% no-repeat;
				}
			}
			.review {
				.review-stars {
					display: inline-block;
					width: 64px;
					height: 17px;
					background: url("/images/stars.png") no-repeat;
					background-size: contain;
					margin-right: 10px;
					vertical-align: bottom;
				}
				.text {
					display: inline-block;
					background-color: #7a869a;
					text-transform: uppercase;
					text-decoration: none;
					color: #fff;
					min-width: 1em;
					padding: 3px 5px 2px;
					line-height: 1;
					font-size: 12px;
					border-radius: 2em;
					text-align: center;
				}
			}
			.downloads {
				font-size: 13px;
				color: #5e6c84;
				text-align: right;
				margin-top: 5px;
			}
		}
		.main-head-wrapper {
			flex: 1 1 auto;
			margin-left: 40px;
			h1 {
				margin-top: 30px;
				margin-bottom: 0;
				color: #253858;
			}
			p {
				margin: 0;
				font-size: 14px;
				line-height: 1.4;
				color: #5e6c84;
			}
			.support {
				background-color: #fff;
				border: 1px solid #ccc;
				border-radius: 3px;
				color: #333;
				display: inline-block;
				font-size: 11px;
				font-weight: 700;
				line-height: 99%;
				padding: 2px 5px;
				text-align: center;
				text-decoration: none;
				text-transform: uppercase;
				margin-top: 15px;
			}
		}
		.deal-wrapper {
			margin-top: 30px;
			text-align: center;
			.try-button {
				color: #172b4d;
				font-weight: 600;
				background-color: #ffc400;
				font-size: 16px;
				line-height: 18px;
				padding: 12px 14px;
				border-radius: 3px;
				display: block;
				text-align: center;
				text-decoration: none;
				outline: 0;
				&:hover {
					background-color: #e6b000;
				}
			}
			.buy-link {
				display: inline-block;
				margin-top: 10px;
				color: #0052cc;
				font-size: 14px;
				&:hover {
					text-decoration: none;
					color: #0065ff;
					outline: 0;
				}
			}
		}
	}

	.nav-bar-wrap {
		background: #fff;
		border: 0 solid #dfe1e6;
		border-top-width: 2px;
		box-shadow: 0 2px 6px -2px rgba(0,0,0,.25);
		.tabs {
			display: flex;
			.tab-link {
				color: #42526e;
				display: inline-block;
				font-size: 18px;
				letter-spacing: 1px;
				padding: 12px 0;
				text-align: center;
				margin-right: 24px;
				cursor: pointer;
				&:hover {
					border: 0 solid #7a869a;
					border-bottom-width: 3px;
				}
				&.current {
					border: 0 solid #0052cc;
					border-bottom-width: 3px;
					color: #0747a6;
				}
			}
		}
	}

	.content {
		margin-top: 30px;
		.tab-content {
			display: none;
			&.current {
				display: block;
			}
		}
		.desc-wrap {
			display: flex;
			align-items: center;
			padding: 30px 0;
			.desc-title {
				color: #5e6c84;
				width: 50%;
				padding: 0 105px;
				font-weight: 400;
				font-size: 24px;
				line-height: 1.25;
				text-align: center;
			}
			.video-container {
				background: url(https://i.ytimg.com/vi/hb9oiI1W51Q/hqdefault.jpg) center no-repeat;
				width: 480px;
				height: 270px;
				position: relative;
				cursor: pointer;
				border: 1px solid #dfe1e6;
				transition: .3s;
				border-radius: 3px;
				text-decoration: none;
				color: #111;
				&:hover {
					opacity: .8;
				}
				.fa-play {
					position: absolute;
					width: 40px;
					height: 40px;
					top: calc(50% - 20px);
					left: calc(50% - 20px);
				}
			}
		}
		.list {
			display: flex;
			justify-content: space-between;
			margin: 60px 0;
			.list-item {
				width: 31%;
				.img {
					max-width: 100%;
					max-height: 100%;
					border-radius: 3px;
				}
				.list-item-title {
					font-size: 24px;
					font-weight: 500;
					line-height: 1.15;
				}
				.list-item-desc {
					font-size: 14px;
				}
			}
		}
	}
	.review-container {
		margin-top: 20px;
		.review-title {
			display: inline-block;
			color: #253858;
			font-size: 24px;
			font-weight: 500;
			line-height: 1.17;
			margin: 0 20px 0 0;
		}
		.review-stars {
			display: inline-block;
			width: 69px;
			height: 17px;
			background: url("/images/stars.png") no-repeat;
			background-size: contain;
			vertical-align: bottom;
			margin-right: 5px;
		}
		.write-review {
			float: right;
			font-size: 14px;
			display: block;
			color: #0052cc;
			margin-top: 10px;
			text-decoration: none;
			&:hover {
				color: #0065ff;
				outline: 0;
			}
		}
		.review-list {
			border-top: 1px solid #dfe1e6;
			margin-top: 10px;
			.review-item {
				display: flex;
				margin: 0 0 30px;
				padding-top: 20px;
				border-top: 1px solid #dfe1e6;
				&:first-of-type {
					border: 0;
				}
				.photo {
					.fa-user {
						width: 48px;
						height: 48px;
						margin-right: 15px;
					}
				}
				.author {
					font-size: 12px;
					color: #5e6c84;
					margin-bottom: 20px;
					margin-top: 5px;
				}
				.text {
					font-size: 14px;
				}
				.helpful {
					margin-top: 20px;
					font-size: 12px;
				}
			}
			.more-reviews {
				display: block;
				text-align: center;
				color: #0052cc;
				&:hover {
					text-decoration: none;
					color: #0065ff;
					outline: 0;
				}
			}
		}
	}
	.apps-slider {
		background: #f4f5f7;
		border: 2px solid #dfe1e6;
		border-left-width: 0;
		border-right-width: 0;
		padding-top: 30px;
		padding-bottom: 30px;
		margin-top: 60px;
		.title {
			font-weight: 500;
			line-height: 1.17;
			font-size: 24px;
			margin-bottom: 10px;
		}
		.slider {
			position: relative;
		}
		.slick-list {
			border: 1px solid #dfe1e6;
			border-radius: 3px;
		}
		.slide {
			display: flex;
			flex-direction: column;
			align-items: center;
			padding: 10px;
			background-color: #fff;
			border-right: 1px solid #dfe1e6;
			text-decoration: none;
			&:hover {
				background-color: #f4f5f7;
			}
			.logo {
				width: 72px;
				height: 72px;
				background: url("https://marketplace-cdn.atlassian.com/files/images/68a717f9-3f46-4bbf-bdbf-03df43e5d7ad.png") 50% 50% no-repeat;

			}
			.slide-title {
				flex: 1 1 auto;
				text-align: center;
				color: #253858;
				height: 63px;
				margin-bottom: 4px;
				font-size: 14px;
			}
			.review-stars {
				display: inline-block;
				width: 69px;
				height: 17px;
				background: url("/images/stars.png") no-repeat;
				background-size: contain;
				vertical-align: bottom;
			}
		}
		.slick-prev,
		.slick-next {
			background: none;
			border: 0;
			outline: none;
			height: 100%;
			position: absolute;
			top: 0;
			.fa-angle-left,
			.fa-angle-right {
				width: 30px;
				height: 30px;
			}
		}
		.slick-prev {
			left: -42px;
		}
		.slick-next {
